* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif !important;
}

html{
  min-height:100%;/* make sure it is at least as tall as the viewport */
  position:relative;
}
body{
  height:100%; /* force the BODY element to match the height of the HTML element */
}

body.night {
  background-color: rgb(36, 36, 36);
  
  .btn-success {
    background-color: #065864;
    border: #065864;
    color: blanchedalmond;
  }

  .btn-outline-success {
    background-color: #065864;
    border: #065864;
    color: blanchedalmond;
  }

  .form-control {
    background-color: teal;
    color: blanchedalmond;
  }

  .navbar {
    background-color: #065864 !important;
  }
}

.container-fluid {
  position:absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  overflow:auto;
  z-index:-1;
  padding-left: 0;
  padding-right: 0;
}

.page {
  padding-top: 55px;
  padding-left: 0;
  padding-right: 0;
  height: 100%;
  width: 100%;
  position: relative;

  @media screen and (max-width: 500px) {
    display: flex;
    flex-direction: column;
  }
}

.title {
  text-align: center;
}

.under-title {
  text-align: center;
  color: rgb(216, 216, 216);
}

.col {
  padding: 0;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}