.tabs {
  position: -webkit-sticky;
  position: sticky;
  background: white;
  z-index: 1000;
  height: 50px;
  width: 100%;
  display: flex;
  align-items: center;

  @media (max-width: 500px) {
    overflow-x: scroll;
    overflow-y: hidden;
    margin-top: 5px
  }
}

body.night {
  .tabs {
    background: rgb(36, 36, 36);
  }
}