.corn-modal {
  height: 400px;
  margin-top: 8%;
  z-index: 1000;

  @media (max-width: 500px) {
    height: 400px;
    margin-top: 18%;
    z-index: 1000;
  }
}

body.night {

  .corn-modal {
    .modal-content {
      background-color: rgb(51, 51, 51);
      color: rgb(206, 206, 206);
    }
  
    .table-words {
      background-color: rgb(51, 51, 51);
      color: white;
    }
  
    .bottom-panel {
      background-color: rgb(51, 51, 51);
      color: white;
    }
  }
}

.table-words {
  height: 400px;
  overflow-y: scroll;
  overflow-x: scroll;
  display: block;
  text-transform: lowercase;

  @media (max-width: 500px) {
    text-transform: lowercase;
    height: 300px;
    width: 100%;
    overflow-y: scroll;
    overflow-x: scroll;
    display: block;
  }
}

.bottom-panel {

  .bottom-panel__inner {
    display: flex;
    justify-content: space-between;

    .inline-block {
      display: inline-block;
    }
  }
}